
import { defineComponent, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import EDMTemplateCard from "@/components/EDMTemplateCard.vue";
import Template1 from "./templates/Template1.vue";
import Template2 from "./templates/Template2.vue";
import Template3 from "./templates/Template3.vue";
import { GetEdms, PostCreateEdm } from "@/typings/store";
import { EdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";

const Component = defineComponent({
  name: "EDM",
  components: {
    EDMTemplateCard,
    Template1,
    Template2,
    Template3
  },
  setup: function() {
    const router = useRouter();
    const store = useStore();

    const list = computed(() => store.state.edm.list);

    const init = async () => {
      await store.dispatch(GetEdms);
    };

    onMounted(async () => await init());

    const onCardClick = async (id: number) => {
      let _id = id;
      if (id < 0) {
        const params: EdmParams = {
          subject: `New EDM`,
          recipient_brand: "Brand Name",
          message: "Text",
          html_message: "<div></div>",
          creator: 1, // TODO 確認之後是否還要帶
          chosen_collection: null,
          chosen_template: Math.abs(id)
        };
        const data = await store.dispatch(PostCreateEdm, params);
        _id = data.id;
        ElNotification({
          title: "Success",
          type: "success",
          message: `Create new EDM successful.`
        });
      }
      await router.push({ name: "EDMDetail", params: { id: _id } });
    };

    return {
      list,

      onCardClick
    };
  }
});

export default Component;
