<template>
  <div id="edm-component">
    <h2 class="zoom-title">Choose Template</h2>
    <el-row :gutter="30">
      <el-col :span="8">
        <EDMTemplateCard @click="onCardClick(-1)">
          <Template1 is-template></Template1>
        </EDMTemplateCard>
        <p class="name">Template 1</p>
      </el-col>
      <el-col :span="8">
        <EDMTemplateCard @click="onCardClick(-2)">
          <Template2 is-template></Template2>
        </EDMTemplateCard>
        <p class="name">Template 2</p>
      </el-col>
      <el-col :span="8">
        <EDMTemplateCard @click="onCardClick(-3)">
          <Template3 is-template></Template3>
        </EDMTemplateCard>
        <p class="name">Template 3</p>
      </el-col>
    </el-row>
    <h2 class="zoom-title">Saved EDM</h2>
    <el-row :gutter="30">
      <el-col
        v-for="item in list.sort((a, b) => b.id - a.id)"
        :key="item.id"
        :span="8"
      >
        <EDMTemplateCard editable @click="onCardClick(item.id)">
          <component
            :is="`Template${item.chosen_template || '1'}`"
            :data="{ brandName: item.recipient_brand, message: item.message }"
          ></component>
        </EDMTemplateCard>
        <p class="name">{{ item.subject }}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import EDMTemplateCard from "@/components/EDMTemplateCard.vue";
import Template1 from "./templates/Template1.vue";
import Template2 from "./templates/Template2.vue";
import Template3 from "./templates/Template3.vue";
import { GetEdms, PostCreateEdm } from "@/typings/store";
import { EdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";

const Component = defineComponent({
  name: "EDM",
  components: {
    EDMTemplateCard,
    Template1,
    Template2,
    Template3
  },
  setup: function() {
    const router = useRouter();
    const store = useStore();

    const list = computed(() => store.state.edm.list);

    const init = async () => {
      await store.dispatch(GetEdms);
    };

    onMounted(async () => await init());

    const onCardClick = async (id: number) => {
      let _id = id;
      if (id < 0) {
        const params: EdmParams = {
          subject: `New EDM`,
          recipient_brand: "Brand Name",
          message: "Text",
          html_message: "<div></div>",
          creator: 1, // TODO 確認之後是否還要帶
          chosen_collection: null,
          chosen_template: Math.abs(id)
        };
        const data = await store.dispatch(PostCreateEdm, params);
        _id = data.id;
        ElNotification({
          title: "Success",
          type: "success",
          message: `Create new EDM successful.`
        });
      }
      await router.push({ name: "EDMDetail", params: { id: _id } });
    };

    return {
      list,

      onCardClick
    };
  }
});

export default Component;
</script>

