<template>
  <div id="edm-template-1-component">
    <div class="left-container">
      <img
        class="top-image"
        src="@/assets/images/edm/bg03.jpg"
        alt="Top Image"
      />
      <div class="content-container">
        <h1>{{ brandName }}</h1>
        <p>
          {{ message }}
        </p>
      </div>
      <img
        class="bottom-image"
        src="@/assets/images/edm/bg06.jpg"
        alt="Bottom Image"
      />
    </div>
    <img
      class="right-image"
      src="@/assets/images/edm/running-girl.png"
      alt="Right Image"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed, PropType } from "vue";
import { templateData, TemplateData } from "@/typings/edm";

const Component = defineComponent({
  name: "Template1",
  props: {
    data: {
      type: Object as PropType<TemplateData>,
      default: () => ({})
    },
    isTemplate: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const { isTemplate, data } = toRefs(props);
    const brandName = computed(() =>
      isTemplate.value ? templateData.brandName : data.value.brandName
    );
    const message = computed(() =>
      isTemplate.value ? templateData.message : data.value.message
    );
    return {
      brandName,
      message
    };
  }
});

export default Component;
</script>

